.stores {
  overflow: scroll;
  height: 100%;
  padding-right: 16px; }
  .stores__title {
    font-weight: 600;
    margin-bottom: 16px;
    font-size: 12px;
    line-height: 18px; }
  .stores__list {
    margin-top: 24px; }
  @supports (-moz-transform: translate(0, 0)) {
    .stores {
      padding-right: 12px; } }
  .stores::-webkit-scrollbar-track {
    background-color: #F2F1F1;
    border-radius: 10px; }
  .stores::-webkit-scrollbar-thumb {
    background-color: #3B3030;
    border-radius: 10px; }
  .stores::-webkit-scrollbar {
    width: 5px;
    height: 0;
    border-radius: 10px;
    background-color: #F2F1F1; }
