.account__button-my-picodi {
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  padding: 12px;
  border-radius: 37px;
  margin: 12px 0 0;
  cursor: pointer;
  color: #3BB54A;
  background: #FFFFFF;
  border: 1px solid #3BB54A; }
  .account__button-my-picodi:hover, .account__button-my-picodi:focus {
    color: #FFFFFF;
    background-color: #236B2C;
    border: 1px solid #236B2C; }

.account__buttons-social {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 16px 0 0; }

.account__button-social {
  height: 44px;
  width: 44px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 8px;
  cursor: pointer;
  transition: transform 0.3s; }
  .account__button-social:hover {
    transform: scale(0.9); }
  .account__button-social--fb {
    background: #004F9D; }
  .account__button-social--google {
    background: #EA4335; }
  .account__button-social--apple {
    background: #000000; }

.account__social-img {
  max-height: 19px;
  width: auto; }

.account__line {
  position: relative;
  display: flex;
  justify-content: center;
  margin: 12px 0; }
  .account__line::before {
    content: '';
    height: 1px;
    width: 100%;
    top: 50%;
    position: absolute;
    background: #CACDCE; }

.account__line-text {
  color: #62686A;
  background: #FFFFFF;
  font-size: 12px;
  text-transform: uppercase;
  position: relative;
  padding: 0 8px; }
