.dropdown-menu {
  background: #FFFFFF;
  box-shadow: 0 3px 6px #00000029;
  position: absolute;
  top: 46px;
  right: 0;
  width: 150px;
  list-style: none;
  padding: 0; }
  .dropdown-menu__item {
    cursor: pointer;
    font-size: 14px;
    color: #3B3030;
    padding: 16px;
    border-bottom: 1px solid #F2F1F1; }
    .dropdown-menu__item:hover {
      background: #F2F1F1; }
    .dropdown-menu__item:last-child {
      border-bottom: 0; }
