.tabs {
  display: flex;
  flex-flow: wrap;
  color: #565A5C;
  margin: 1px 0 0;
  padding: 0;
  justify-content: space-between; }

.tab__button {
  padding: 21px 0 14px;
  cursor: pointer;
  color: #3B3030;
  background: #FFFFFF;
  flex: 1 1 auto;
  font-size: 12px;
  text-align: center;
  border-bottom: 4px solid #FFFFFF; }
  .tab__button--active {
    font-weight: 600;
    color: #3BB54A;
    border-color: #3BB54A; }
  .tab__button--disabled {
    color: #CECECE;
    cursor: default; }

.content {
  flex: 1 1 100%;
  height: 339px; }
