.tabs {
  display: flex;
  flex-flow: wrap;
  color: #565A5C;
  margin: 12px 0 0;
  padding: 0 16px;
  justify-content: space-between; }

.tab__button {
  padding: 11px 0 10px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  cursor: pointer;
  color: #7F7F80;
  background: #E4E4E4;
  flex: 1 1 auto;
  font-size: 10px;
  text-align: center; }
  .tab__button:nth-child(2) {
    margin: 0 1px; }
  .tab__button:hover {
    background: #FFFFFF; }
  .tab__button--active {
    font-weight: 600;
    color: #3B3030;
    background: #FFFFFF; }

.content {
  flex: 1 1 100%;
  padding: 16px 12px;
  background: #FFFFFF;
  height: 362.5px; }
