.picodi-app {
  color: #3B3030;
  cursor: default;
  direction: ltr;
  font-family: 'pico-ext-noto-sans', 'Roboto', 'Arial', sans-serif;
  letter-spacing: -0.01em;
  line-height: normal;
  text-align: left;
  text-indent: initial;
  text-transform: none;
  visibility: visible;
  word-spacing: normal;
  font-weight: normal; }

/deep/ .notice-promt-fade-out-enter-active, /deep/ .notice-promt-fade-out-leave-active {
  transition: opacity 0.5s ease; }

/deep/ .notice-promt-fade-out-enter-from, /deep/ .notice-promt-fade-out-leave-to {
  opacity: 0; }

/deep/ .icon-svg {
  display: block;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain; }
