.modal-success__image {
  max-height: 200px; }

.modal-success__title {
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.18px;
  color: #3B3030;
  text-align: center;
  font-weight: 600;
  margin-top: 24px;
  margin-bottom: 5px; }

.modal-success__title--cashback {
  font-weight: 400;
  margin-bottom: 0;
  margin-top: 32px; }

.modal-success__title > > > b {
  font-weight: 600; }

.modal-success__title > > > span {
  font-weight: 600;
  text-transform: uppercase;
  color: #3BB54A; }

.modal-success__title > > > span:before {
  content: '';
  display: block; }

> > > .modal-success__title--small {
  font-size: 14px;
  font-weight: 400;
  text-decoration: line-through; }

.modal-success__row {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.16px;
  width: 100%;
  margin: 8px 0; }

.modal-success__code {
  font-weight: 600; }

.modal-success__picodi-price {
  font-weight: 600;
  color: #3BB54A;
  font-size: 20px;
  line-height: 18px;
  letter-spacing: -0.2; }

.modal-success__description {
  font-size: 14px;
  line-height: 19px;
  letter-spacing: -0.14px;
  margin: 0 0 12px;
  color: #565A5C; }

.modal-success__description--cashback {
  color: #3B3030; }
