.general {
  position: relative;
  height: 100%;
  padding-right: 16px;
  overflow-y: auto; }
  @supports (-moz-transform: translate(0, 0)) {
    .general {
      padding-right: 12px; } }
  .general::-webkit-scrollbar-track {
    background-color: #CECECE;
    border-radius: 10px; }
  .general::-webkit-scrollbar-thumb {
    background-color: #3B3030;
    border-radius: 10px; }
  .general::-webkit-scrollbar {
    width: 5px;
    height: 0;
    border-radius: 10px;
    background-color: #F2F1F1; }
  .general__check-box {
    margin-bottom: 20px; }
  .general__my-picodi-account {
    display: inline-flex; }
  .general__button {
    flex-basis: 100%;
    background: #3BB54A;
    color: #FFFFFF;
    border: 0;
    padding: 10px;
    margin: 8px 0 0;
    font-size: 12px;
    font-weight: 600;
    border-radius: 23px;
    cursor: pointer;
    width: 100%;
    text-decoration: none; }
    .general__button:hover {
      background: #3BB54A; }
  .general__select {
    margin-bottom: 20px; }
  .general__title {
    font-size: 14px;
    font-weight: 600;
    line-height: 19px;
    color: #3B3030;
    margin-bottom: 0; }
  .general__description {
    color: #565A5C;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    margin: 4px 0 8px; }

::v-deep .checkbox {
  justify-content: flex-start;
  margin: 0; }
  ::v-deep .checkbox__title {
    font-size: 14px;
    line-height: 19px;
    margin-left: 8px;
    order: 2; }
  ::v-deep .checkbox__label {
    order: 1; }
