.checkbox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 0 16px;
  font-size: 12px;
  line-height: 17px;
  font-weight: 400; }
  .checkbox__titile {
    width: 90%; }
  .checkbox__label {
    display: inline-block;
    height: 19px;
    width: 33px;
    position: relative; }
  .checkbox__input {
    display: none; }
  .checkbox__slider {
    background-color: #F2F1F1;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: .4s;
    border-radius: 25px; }
  .checkbox__slider:before {
    background-color: #FFFFFF;
    bottom: 2px;
    left: 2px;
    content: '';
    height: 15px;
    width: 15px;
    position: absolute;
    transition: .4s;
    border-radius: 50%; }
  .checkbox__input:checked + .checkbox__slider, .checkbox__input:disabled + .checkbox__slider {
    background-color: #3BB54A; }
  .checkbox__input:checked + .checkbox__slider:before, .checkbox__input:disabled + .checkbox__slider:before {
    transform: translateX(15px);
    left: 0; }
  .checkbox__input:disabled + .checkbox__slider {
    opacity: 0.5; }
  #cashabck:disabled + .checkbox__slider:before {
    transform: none;
    left: 2px; }
  #cashabck:disabled + .checkbox__slider {
    background-color: #F2F1F1; }
