<template>
  <div class="progress">
    <div class="progress-bar" :class="classes" :style="`${customStyle} width: ${percentage}%;`" aria-valuemax="100"></div>
  </div>
</template>

<script>
  export default {
    name: "ProgressBar",
    props: {
      percentage: {type: Number, default: 100},
      delay: {type: Number, default: 0},
      bg: {type: String, default: 'primary'},
      customBgColor: {type: String, default:null},
      animation: {type: Boolean, default: false},
      striped: {type: Boolean, default: false},
    },
    data() {
      return{
        classes: null,
        customStyle: ""
      }
    },
    created() {
      this.classes = 'bg-' + this.bg
      if(this.customBgColor!=null){this.customStyle = ` background-color:${this.customBgColor}!important;`}

      if(this.animation){this.classes = this.classes +  " progress-bar-animated"}
      if(this.striped){this.classes = this.classes +  " progress-bar-striped"}
    }
  }
</script>

<style scoped>
  .progress {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 10px;
    overflow: hidden;
    font-size: .75rem;
    background-color: #e9ecef;
    border-radius: .25rem;
    margin: 0;
    transform: none;
  }

  .progress-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: $white-color;
    text-align: center;
    background-color: #007bff;
    transition: width .6s ease
  }

  .progress-bar-striped {
    background-image: linear-gradient(45deg, rgba(255, 255, 255, .15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%, transparent 75%, transparent);
    background-size: 1rem 1rem
  }

  .bg-success {
    background-color: #28a745 !important
  }
</style>