.no-cashback-prompt.main {
  overflow-y: auto;
  padding-right: 12px; }

.no-cashback-prompt__description {
  font-size: 16px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }
  .no-cashback-prompt__description > p {
    margin: 0; }
  .no-cashback-prompt__description > > > b {
    color: #3BB54A;
    text-transform: uppercase;
    font-weight: 700; }
  .no-cashback-prompt__description--last-child {
    margin-bottom: 16px; }
  .no-cashback-prompt__description--large {
    font-size: 18px;
    margin-bottom: 16px; }
    .no-cashback-prompt__description--large > b:after {
      content: '';
      display: block; }

.no-cashback-prompt__ico {
  height: 20px;
  width: 20px;
  margin-left: 6px; }

.no-cashback-prompt__shops {
  display: flex;
  flex-direction: column;
  padding: 0 0 8px;
  margin-top: 16px; }

.no-cashback-prompt__shop {
  display: grid;
  grid-template-columns: 110px 35px 90px;
  align-items: center;
  background: #FFFFFF;
  border-radius: 4px;
  padding: 12px;
  margin-bottom: 12px;
  justify-content: space-between;
  text-align: center;
  cursor: pointer; }

.shop__figure {
  width: 100px; }

.shop__description {
  display: flex;
  flex-direction: column; }

.shop__img {
  max-width: 100px; }

.shop__coin {
  height: 30px;
  width: auto;
  margin: 0 auto; }

.shop__value {
  font-size: 16px;
  font-weight: 700;
  color: #3BB54A;
  text-transform: lowercase; }

.shop__cashback {
  font-size: 10px;
  text-transform: uppercase; }
