.countdown {
  background: #181A1B;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4px 8px 8px 8px;
  text-align: center; }
  .countdown__title {
    padding-bottom: 4px;
    font-size: 12px;
    color: #FFFFFF; }
  .countdown__timer {
    display: flex; }
  .countdown__timer-box {
    padding-right: 10px;
    display: flex;
    align-items: baseline; }
    .countdown__timer-box:last-child {
      margin-right: 0; }
  .countdown__time {
    font-size: 12px;
    padding: 2px 4px;
    margin: 0 2px;
    border-radius: 4px;
    background: #FFAF57;
    color: #662600; }
  .countdown__unit {
    font-size: 10px;
    color: #FFFFFF; }
