.offer {
  background: #FFFFFF;
  border-radius: 3px;
  padding: 12px; }
  .offer:not(:first-child) {
    margin-top: 12px; }
  .offer__title {
    color: #3B3030;
    font-size: 14px;
    line-height: 18px;
    margin: 4px 0 0;
    letter-spacing: -0.35px;
    cursor: pointer; }
    .offer__title:hover {
      color: #000000; }

.discount {
  box-shadow: inset 0 0 0 1px #CECECE;
  border-radius: 4px;
  height: 80px;
  width: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
  float: left;
  margin-right: 12px;
  margin-bottom: 12px; }
  .discount__value {
    align-self: flex-end;
    font-size: 16px;
    font-weight: 600;
    line-height: 30px; }
    .discount__value--small {
      font-size: 14px;
      line-height: 24px; }
    .discount__value > .icon-svg {
      min-width: 25px;
      min-height: 25px; }
  .discount__caption {
    text-transform: uppercase;
    color: #FFFFFF;
    align-self: flex-end;
    width: 100%;
    padding: 3px 0;
    font-weight: 600;
    text-align: center;
    font-size: 9px;
    line-height: 14px; }
  .discount--code {
    color: #E31E26; }
    .discount--code .discount__caption {
      background: #E31E26; }
  .discount--promo {
    color: #FF7F32; }
    .discount--promo .discount__caption {
      background: #FF7F32; }

.btn {
  clear: both;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 100%;
  position: relative;
  font-size: 12px;
  font-weight: 600;
  line-height: 17px;
  background-color: #3BB54A;
  color: #FFFFFF;
  border-radius: 30px;
  border: 0;
  user-select: none;
  outline: none;
  cursor: pointer; }
  .btn:hover, .btn:focus {
    background-color: #236B2C; }
  .btn--code {
    background: #FFFFFF; }
    .btn--code:before {
      content: "";
      position: absolute;
      width: 85%;
      height: 100%;
      left: 0;
      top: 0;
      border-top-left-radius: 30px;
      border-bottom-left-radius: 30px;
      transition: width .2s ease-out;
      z-index: 5;
      background-color: #3BB54A; }
    .btn--code:after {
      border-color: transparent transparent transparent #2e8d3a;
      left: 85%;
      top: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 40px 0 0 35px;
      transform: rotate(17deg);
      transition: left .2s ease-out;
      transform-origin: top left;
      content: "";
      position: absolute;
      z-index: 5;
      display: block; }
    .btn--code:hover {
      background-color: white; }
      .btn--code:hover:before {
        background-color: #236B2C;
        width: 80%; }
      .btn--code:hover::after {
        left: 80%; }
  .btn--visited {
    cursor: text; }
    .btn--visited::before, .btn--visited::after, .btn--visited .btn__label {
      display: none; }
    .btn--visited .btn__code {
      justify-content: center;
      font-size: 14px;
      padding: 0 70px 0 8px;
      user-select: all; }
  .btn__label {
    width: 100%;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center; }
    .btn__label--copy::after {
      content: '';
      background-image: url(../../../static/images/copy-ico.svg);
      height: 16px;
      width: 14px;
      margin-left: 9px;
      display: inline-block; }
  .btn__code {
    background-color: initial;
    border: 1px solid #cecece;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 12px;
    width: 100%;
    height: 100%;
    line-height: 22px;
    z-index: 0;
    color: #000000;
    border-radius: 30px;
    font-size: 12px;
    font-weight: 600; }
  .btn__copy {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background-color: #3BB54A;
    display: flex;
    align-items: center;
    font-size: 12px;
    padding: 0 15px;
    border-radius: 30px;
    z-index: 1;
    cursor: pointer; }
    .btn__copy:hover {
      background-color: #236B2C; }
    .btn__copy::after {
      content: '';
      background-image: url(../../../static/images/copy-ico.svg);
      height: 16px;
      width: 14px;
      margin-left: 6px; }
    .btn__copy--copied_code {
      background-color: #236B2C; }
      .btn__copy--copied_code::after {
        display: none; }

.shop-name {
  font-size: 16px;
  line-height: 24px;
  flex: 1;
  text-align: center; }
