.account {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0; }
  .account__logo {
    max-height: 100px;
    margin-bottom: 12px; }
  .account__title {
    color: #3B3030;
    font-weight: 600;
    font-size: 16px;
    text-align: center; }
  .account__description {
    color: #7F7F80;
    font-size: 12px;
    line-height: 17px;
    text-align: center;
    margin-bottom: 4px; }
  .account__container {
    background: #FFFFFF;
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 5px; }
  /deep/ .account__foot {
    text-align: center;
    font-size: 12px;
    color: #3B3030;
    margin-top: 12px; }
  /deep/ .account__link {
    color: #0277BD;
    cursor: pointer;
    margin-top: auto; }
    /deep/ .account__link:hover {
      text-decoration: underline; }
    /deep/ .account__link--inline {
      display: inline; }
