/deep/ img {
  border: none;
  position: static;
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  outline: none; }
  /deep/ img:hover, /deep/ img:focus {
    border: none;
    outline: none; }

/deep/ *, *:before, *:after {
  font-family: 'pico-ext-noto-sans', 'Roboto', 'Arial', sans-serif !important;
  font-weight: 400;
  box-sizing: border-box !important; }

/deep/ ul, ol {
  margin: 0;
  list-style: none; }

/deep/ .app__container {
  position: fixed;
  top: 80px;
  right: 80px;
  background: #F2F1F1;
  width: 300px;
  height: 510px;
  z-index: 999999999999999;
  box-shadow: 0 3px 30px #0000004D;
  display: flex;
  flex-direction: column;
  border-radius: 5px; }
  /deep/ .app__container--logged-user {
    height: 524.5px; }
    /deep/ .app__container--logged-user .main {
      margin-bottom: 1.5px; }
  /deep/ .app__container--countdown {
    height: 577.5px; }
  /deep/ .app__container--customprompt {
    height: auto;
    top: 150px; }
  /deep/ .app__container--noticeprompt {
    min-height: 0;
    height: auto; }
  /deep/ .app__container--cashback {
    height: auto; }
  /deep/ .app__container--is-recognize {
    top: 150px; }

/deep/ .app__blur {
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vw;
  content: '';
  z-index: 9999; }

/deep/ .app__header {
  min-height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  padding: 0 16px;
  border-radius: 5px 5px 0 0; }

/deep/ .app__content {
  height: 100%; }

/deep/ .app__logged-user {
  padding: 0 12px;
  margin: 6px 0;
  font-size: 10px;
  text-align: center; }
  /deep/ .app__logged-user b {
    color: #3B3030;
    font-weight: 600; }

/deep/ .app__footer {
  min-height: 45px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #FFFFFF;
  padding: 0 16px;
  margin-top: auto;
  border-radius: 0 0 5px 5px; }

/deep/ .app__footer-icon {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: 0;
  outline: 0;
  background-color: white;
  padding: 4px 8px;
  font-size: 12px;
  line-height: 24px; }
  /deep/ .app__footer-icon:hover {
    color: #3B3030; }
    /deep/ .app__footer-icon:hover > .icon-svg {
      background-color: #3B3030; }
  /deep/ .app__footer-icon > .icon-svg {
    min-width: 24px;
    min-height: 24px;
    margin-right: 8px; }

/deep/ .app__title {
  font-size: 12px;
  line-height: 14px;
  color: #3B3030;
  font-weight: 600;
  flex: 2;
  text-align: center;
  padding: 0 5px; }

/deep/ .app__error {
  padding: 15px;
  font-size: 14px;
  font-style: italic; }

/deep/ .logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
  height: 27px !important; }
  /deep/ .logo__img {
    height: 27px !important;
    width: auto; }
  /deep/ .logo__go-back {
    position: relative;
    display: inline-flex;
    padding: 5px;
    cursor: pointer;
    justify-content: center; }
  /deep/ .logo__go-back-icon {
    display: block;
    width: 11px;
    height: 11px;
    background-color: #FFFFFF;
    border-top: 0;
    border-right: 0;
    border-bottom: 2px solid #3B3030;
    border-left: 2px solid #3B3030;
    transform: translateX(25%) rotate(45deg); }

/deep/ .main {
  padding-left: 12px;
  margin: 12px 0;
  overflow-y: scroll;
  overflow-x: hidden; }
  @supports (-moz-transform: translate(0, 0)) {
    /deep/ .main {
      padding-right: 12px; } }
  /deep/ .main::-webkit-scrollbar-track {
    background-color: #FFFFFF;
    border-right: 12px solid #F2F1F1;
    border-left: 12px solid #F2F1F1; }
  /deep/ .main::-webkit-scrollbar {
    width: 29px;
    background-color: #F2F1F1; }
  /deep/ .main::-webkit-scrollbar-thumb {
    background-color: #565A5C;
    border-right: 12px solid #F2F1F1;
    border-left: 12px solid #F2F1F1; }

.nav-app {
  position: relative;
  list-style: none;
  padding: 0;
  flex: 1;
  display: flex;
  justify-content: flex-end; }
  .nav-app__item {
    position: relative; }
    .nav-app__item > .icon-svg {
      margin: 3px;
      min-height: 24px;
      min-width: 24px;
      cursor: pointer; }
      .nav-app__item > .icon-svg:hover {
        background-color: #3B3030; }

.close-app:hover .close-app__tooltip {
  display: block; }

.close-app__tooltip {
  display: none;
  position: absolute;
  background: #FFFFFF;
  top: 100%;
  left: 0;
  min-width: 100px;
  font-size: 10px;
  padding: 2.5px 5px;
  border: 1px solid #E5E5E5;
  border-radius: 4px;
  text-align: center; }

.search,
.settings {
  font-size: 16px;
  line-height: 16px; }
