.app__promo-icon {
  position: fixed;
  top: 150px;
  right: 0;
  z-index: 999999999; }

.promo__container {
  position: relative; }

.promo__btn {
  cursor: pointer; }

.promo__icon-container {
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  background-color: #FFFFFF;
  box-shadow: 0 3px 6px #00000029;
  height: 64px;
  width: 64px;
  display: flex;
  align-items: center;
  justify-content: center; }

.promo__icon {
  padding: 15px;
  width: 64px;
  height: 64px;
  box-sizing: border-box; }

.promo__counter-container {
  background-color: #3BB54A;
  color: #FFFFFF;
  position: absolute;
  border-radius: 50px;
  bottom: 7px;
  left: 7px;
  padding: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  font-size: 12px; }
