.custom-prompt {
  min-height: 415px;
  height: 0;
  padding-top: 4px; }
  .custom-prompt__shops {
    display: flex;
    flex-direction: column;
    padding: 0 0 8px;
    margin-top: 8px; }
  .custom-prompt__shop {
    display: grid;
    grid-template-columns: 110px 35px 80px;
    align-items: center;
    background: #FFFFFF;
    border-radius: 4px;
    padding: 17.5px 8px;
    margin-bottom: 8px;
    justify-content: space-between;
    text-align: center;
    cursor: pointer; }

.shop__figure {
  width: 100px;
  padding: 8px;
  border-radius: 6px; }

.shop__description {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.shop__img {
  max-width: 100px; }

.shop__coin {
  height: 30px;
  width: auto;
  margin: 0 auto; }

.shop__value {
  font-size: 16px;
  font-weight: 700;
  color: #3BB54A;
  text-transform: lowercase; }
  .shop__value--through {
    font-size: 10px;
    color: #3B3030;
    text-decoration: line-through; }

.shop__cashback {
  font-size: 10px;
  color: #62686A; }
