::v-deep .custom-prompt {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  margin: 12px 16px; }
  ::v-deep .custom-prompt__title {
    font-weight: 600; }
  ::v-deep .custom-prompt__sub-title {
    font-size: 14px; }
  ::v-deep .custom-prompt__custom-title {
    font-weight: 600;
    font-size: 14px; }
  ::v-deep .custom-prompt__custom-sub-title {
    font-size: 10px;
    color: #62686A; }
