.start {
  height: 470px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .start__container {
    background: #FFFFFF;
    width: 90%;
    padding: 25px 16px;
    border-radius: 5px; }
  .start__hero {
    display: flex;
    justify-content: center; }
  .start__logo {
    flex: 0 0 33px;
    width: 33px;
    margin-right: 8px; }
  .start__content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex: 0 0 auto; }
  .start__title {
    font-size: 14px;
    font-weight: 600;
    color: #3B3030; }
  .start__description {
    font-size: 12px;
    color: #7F7F80; }
  .start__country {
    margin-top: 16px; }
  .start__country-title {
    font-size: 12px;
    font-weight: 600;
    color: #3B3030;
    margin-bottom: 8px; }
