.search {
  padding: 12px;
  position: relative; }
  .search__wrapper {
    position: relative;
    display: block; }
  .search__input {
    display: flex;
    align-items: center;
    border-radius: 43px;
    width: 100%;
    height: 40px;
    padding: 0 48px 0 16px;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: -0.3px;
    border: 1px solid #eee;
    text-decoration: none;
    text-align: left;
    position: static;
    margin: 0;
    font-weight: 400;
    color: #565A5C;
    background-color: #FFFFFF; }
    .search__input:hover, .search__input:focus {
      outline: none; }
      .search__input:hover + .search__icon, .search__input:focus + .search__icon {
        background-color: #000000; }
    .search__input:focus {
      border: 1px solid #DDDDDD; }
  .search__icon {
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
    min-width: 24px;
    min-height: 24px;
    cursor: text; }
  .search__loader {
    z-index: 1;
    width: 100%;
    height: 100%;
    text-align: center; }
    .search__loader::before {
      content: '';
      position: absolute;
      width: 30px;
      height: 30px;
      background: 0 0;
      border-top: 2px solid red;
      border-right: 2px solid transparent;
      border-radius: 50%;
      animation-name: spin;
      animation-duration: .5s;
      animation-timing-function: linear;
      animation-iteration-count: infinite; }

@keyframes spin {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }

.shops {
  position: relative;
  height: 344px;
  margin: 0 !important; }

.shop {
  display: grid;
  grid-template-columns: 68px auto 80px;
  align-items: center;
  grid-gap: 10px;
  padding: 12px;
  background-color: #FFFFFF;
  position: relative;
  cursor: pointer;
  border-radius: 4px; }
  .shop:not(:first-child) {
    margin-top: 12px; }
  .shop__figure {
    height: 55px;
    display: flex;
    justify-content: center; }
  .shop__img {
    width: auto;
    max-width: 100%;
    align-self: center; }
  .shop__name {
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: -0.35px;
    color: #3B3030;
    margin-top: 0;
    margin-bottom: 4px;
    border: 0;
    overflow-wrap: anywhere; }
  .shop__offers-length {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.3px;
    display: flex; }
  .shop__offers-code, .shop__offers-promo {
    font-weight: 600;
    display: flex;
    align-items: center; }
    .shop__offers-code::after, .shop__offers-promo::after {
      height: 16px;
      width: 16px;
      content: '';
      display: block;
      -webkit-mask-position: center;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-size: contain;
      margin-left: 2px; }
  .shop__offers-code {
    color: #E31E26;
    padding-right: 4px; }
