.cashback-activated {
  background: #FFFFFF;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  margin: 12px 16px;
  padding: 25px 16px;
  text-align: center;
  justify-content: center;
  min-height: 415px; }
  .cashback-activated__activated {
    color: #3BB54A;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600; }
  .cashback-activated__image {
    max-width: 96px;
    margin: 16px auto 18px; }
  .cashback-activated__title {
    color: #3B3030;
    font-size: 14px;
    margin-bottom: 5px;
    font-weight: 600; }
  .cashback-activated__descritpion {
    color: #3B3030;
    font-size: 14px; }
  .cashback-activated__button {
    flex-basis: 100%;
    background: #3BB54A;
    color: #FFFFFF;
    border: 0;
    padding: 13px;
    margin: 16px 0 0;
    font-size: 14px;
    font-weight: 600;
    border-radius: 23px;
    cursor: pointer;
    width: 100%; }
    .cashback-activated__button:hover {
      background: #236B2C; }
