.cart {
  margin: 24px 12px; }
  .cart__content {
    padding: 32px 16px 24px 16px;
    height: 100%;
    width: 100%;
    background-color: #FFFFFF;
    display: flex;
    color: #3B3030;
    flex-direction: column;
    align-items: center;
    text-align: center; }
  .cart__title {
    color: #3B3030;
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.18px;
    text-align: center;
    margin-top: 0;
    margin-bottom: 32px; }
    .cart__title > > > b {
      color: #3BB54A;
      font-weight: 600; }
  .cart__image {
    min-height: 135px; }
  .cart__middle {
    margin-top: auto;
    margin-bottom: auto; }
  .cart__text {
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: -0.12px;
    padding: 0 32px; }
  .cart__test-button {
    font-size: 12px;
    font-weight: 600;
    text-align: center;
    color: #FFFFFF;
    background: #3BB54A;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 37px;
    margin: 32px 0 0;
    cursor: pointer; }
    .cart__test-button:hover, .cart__test-button:focus {
      background-color: #236B2C; }
