.cashback .active {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 16px;
  border-radius: 4px;
  background: #FFFFFF; }
  .cashback .active__title {
    color: #3B3030;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase; }
  .cashback .active__value {
    color: #3BB54A;
    font-size: 16px;
    font-weight: 400;
    margin: 0; }
    .cashback .active__value--through {
      font-size: 10px;
      color: #3B3030;
      text-decoration: line-through; }
  .cashback .active__numbers {
    display: flex;
    justify-content: flex-end; }
  .cashback .active__offers-code, .cashback .active__offers-promo {
    font-weight: 400;
    display: flex;
    align-items: center;
    font-size: 16px; }
    .cashback .active__offers-code::after, .cashback .active__offers-promo::after {
      height: 16px;
      width: 16px;
      content: '';
      display: block;
      -webkit-mask-position: center;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-size: contain;
      margin-left: 4px; }
