  .cashback .active__button {
    flex-basis: 100%;
    background: #3BB54A;
    color: #FFFFFF;
    border: 0;
    padding: 13px;
    margin: 12px 0 0;
    font-size: 14px;
    font-weight: 400;
    border-radius: 23px;
    cursor: pointer; }
    .cashback .active__button:hover {
      background: #236B2C; }
    .cashback .active__button--disabled {
      background: #F2F1F1;
      color: #565A5C; }
      .cashback .active__button--disabled:hover {
        background: #F2F1F1; }
  .cashback .active__button-svg {
    width: 14px;
    height: 10px;
    display: inline-block;
    margin-right: 8px; }
  .cashback .active__cashback-date {
    color: #565A5C;
    font-size: 10px;
    width: 100%;
    text-align: center;
    margin-top: 8px; }

.cashback .description {
  margin-top: 16px;
  padding: 16px;
  border-radius: 4px;
  background: #FFFFFF; }
  .cashback .description__strong {
    font-weight: 600;
    color: #3B3030; }
  .cashback .description__title {
    color: #3B3030;
    font-weight: 600;
    font-size: 12px;
    margin-bottom: 12px;
    display: block; }
    .cashback .description__title--information {
      margin-top: 16px; }
  .cashback .description__confirmation-time {
    display: flex;
    justify-content: space-between;
    font-size: 12px; }
    .cashback .description__confirmation-time--avg {
      margin: 0 0 12px; }
    .cashback .description__confirmation-time--max {
      margin: 0; }
  .cashback .description__information {
    margin: 0;
    white-space: pre-line;
    font-size: 12px; }
    .cashback .description__information br {
      margin-bottom: 12px;
      content: '';
      display: block; }
    .cashback .description__information--bonus {
      font-size: 12px;
      border-radius: 8px;
      padding: 12px;
      background: #ECF9FE;
      border: 1px solid #077FC5;
      color: #000000; }
