/deep/ .modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999999;
  width: 500px;
  min-height: 580px;
  background-color: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0 0 30px #00000080;
  padding: 0 32px;
  box-sizing: border-box;
  justify-content: center;
  display: flex;
  align-items: center; }
  /deep/ .modal__content {
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center; }
  /deep/ .modal__image-container {
    display: flex;
    align-items: center;
    justify-content: center; }
  /deep/ .modal__title {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.18px;
    margin-bottom: 24px;
    color: #3B3030;
    text-align: center; }
  /deep/ .modal__button {
    font-size: 14px;
    line-height: 19px;
    letter-spacing: -0.14px;
    font-weight: 600;
    text-align: center;
    color: #FFFFFF;
    background: #3BB54A;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 37px;
    margin: 12px 0 0;
    cursor: pointer;
    width: 300px; }
    /deep/ .modal__button:hover, /deep/ .modal__button:focus {
      background-color: #236B2C; }
  /deep/ .modal__close {
    position: absolute;
    top: 5px;
    right: 5px;
    height: 24px;
    width: 24px;
    opacity: 0.2;
    cursor: pointer; }
    /deep/ .modal__close:hover {
      opacity: 1; }

.app__blur {
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vw;
  content: '';
  z-index: 9999; }
