.notice-prompt {
  background: #FFFFFF;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  margin: 12px 16px;
  padding: 25px 16px;
  text-align: center;
  justify-content: center; }
  .notice-prompt__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #E5E5E5;
    padding-bottom: 7.5px; }
  .notice-prompt__shop-logo {
    display: flex; }
  .notice-prompt__shop-img {
    max-width: 100px; }
  .notice-prompt__cashback {
    font-size: 10px;
    text-transform: uppercase; }
  .notice-prompt__cashback-value {
    display: block;
    color: #3BB54A;
    font-weight: 800; }
    .notice-prompt__cashback-value--through {
      font-size: 10px;
      color: #3B3030;
      text-decoration: line-through;
      font-weight: 400; }
  .notice-prompt__animation-container {
    max-width: 108px;
    min-height: 108px;
    align-self: center; }
  .notice-prompt__title {
    font-size: 14px;
    color: #3BB54A;
    font-weight: 800;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center; }
  .notice-prompt__check {
    height: 14px;
    width: 14px;
    margin-left: 5px; }
  .notice-prompt__description {
    font-size: 12px; }
