.modal-error__image {
  max-height: 200px; }

.modal-error__title {
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.18px;
  color: #3B3030;
  text-align: center;
  font-weight: 600;
  margin-top: 24px;
  margin-bottom: 5px; }
  .modal-error__title--cashback {
    font-weight: 400;
    margin-bottom: 0;
    margin-top: 32px; }
  .modal-error__title > > > b {
    font-weight: 600; }
  .modal-error__title > > > span {
    font-weight: 600;
    text-transform: uppercase;
    color: #3BB54A; }
    .modal-error__title > > > span:before {
      content: '';
      display: block; }
  > > > .modal-error__title--small {
    font-size: 14px;
    font-weight: 400;
    text-decoration: line-through; }

.modal-error__description {
  font-size: 14px;
  line-height: 19px;
  letter-spacing: -0.14px;
  margin: 0 0 12px;
  color: #565A5C; }
  .modal-error__description--cashback {
    color: #3B3030; }
