.cashback-prompt {
  background: #FFFFFF;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  margin: 12px 16px;
  padding: 25px 16px;
  text-align: center;
  justify-content: center;
  min-height: 415px; }
  .cashback-prompt__image {
    max-width: 187px;
    margin: 0 auto 16px; }
  .cashback-prompt__title {
    color: #3B3030;
    font-size: 16px;
    font-weight: 600;
    text-align: center; }
  .cashback-prompt__volume {
    display: flex;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    margin-top: 16px;
    padding: 16px 0;
    border-top: 1px solid #F2F1F1;
    border-bottom: 1px solid #F2F1F1;
    justify-content: space-between; }
  .cashback-prompt__value {
    color: #3BB54A; }
  .cashback-prompt__terms {
    font-size: 12px;
    margin: 12px 0 0;
    color: #7F7F80;
    cursor: pointer; }
    .cashback-prompt__terms:hover {
      text-decoration: underline; }
  .cashback-prompt__button {
    flex-basis: 100%;
    background: #3BB54A;
    color: #FFFFFF;
    border: 0;
    padding: 13px;
    margin: 12px 0 0;
    font-size: 14px;
    font-weight: 600;
    border-radius: 23px;
    cursor: pointer;
    width: 100%; }
    .cashback-prompt__button:hover {
      background: #236B2C; }
  .cashback-prompt__close-link {
    position: relative;
    margin-top: 24px; }
    .cashback-prompt__close-link:hover .cashback-prompt__close-link-tooltip {
      display: block; }
  .cashback-prompt__close-link-tooltip {
    display: none;
    position: absolute;
    background: #FFFFFF;
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    min-width: 100px;
    font-size: 10px;
    padding: 3px 8px;
    border: 1px solid #E5E5E5;
    border-radius: 4px;
    text-align: center; }
  .cashback-prompt__close-link-text {
    cursor: pointer;
    color: #BF2B2D;
    font-size: 12px; }
